import React, { useState, useEffect } from 'react';
import { 
    Icon,
    Grid,
    Menu,
    MenuItem,
    Avatar,
    IconButton,
    Card,
    TextField
} from '@mui/material';
import FormControl from '@mui/material/FormControl'
import { Span } from 'app/components/Typography'
import Button from '@mui/material/Button'
import { styled } from '@mui/system';
import { useNavigate } from 'react-router-dom';
import useAuth from 'app/hooks/useAuth';
import { tabsData } from 'app/constants/data';
import CardHeader from '@mui/material/CardHeader';
import moment from 'moment';
import { ValidatorForm, TextValidator, SelectValidator } from 'react-material-ui-form-validator';
import { getUserDetails } from 'app/services/userService';
import UserProfileDetail from 'app/components/UserProfileDetail/UserProfileDetail';
import defaultImg from 'app/assets/default-profile-pic.jpg';
import SessionModal from 'app/components/SessionModal/SessionModal';
import './Profile.css';

const CardRoot = styled(Card)(() => ({
    height: '100%',
    padding: '20px 24px',
    // textAlign: 'center',
}))

// const TextField = styled(TextValidator)(() => ({
//     width: '100%',
//     marginBottom: '16px',
// }))

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
    marginTop: '8px'
}))


function setInfo(user) {
    let info = {};
    if(user && user.role && user.role.type === 'admin') {
        info = tabsData['adminProfile']['profileInfo'];
    } else if(user && user.role && user.role.type === 'center') {
        info = tabsData['centerProfile']['profileInfo'];
    } else if (user && user.role && user.role.type === 'counsellor') {
        info = tabsData['counsellorProfile']['profileInfo'];
    }
    return info;
}

const ITEM_HEIGHT = 48;

const Profile = () => {
    const navigate = useNavigate();
    const { user } = useAuth();
    const [anchorEl, setAnchorEl] = useState(null);
    const [profileInfo, setProfileInfo] = useState(() => setInfo(user));
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');

    const open = Boolean(anchorEl);
    const isAdmin = user && user.role && user.role.type === 'admin' ? true : false;
    const isCenter = user && user.role && user.role.type === 'center' ? true : false;
    const isCounsellor = user && user.role && user.role.type === 'counsellor' ? true : false;
    let adminInfo, centerInfo, counsellorInfo;
    if(isCenter){
        centerInfo = {...profileInfo};
        delete centerInfo['logo'];
        delete centerInfo['logo_image'];
        delete centerInfo['domain'];
    } else if (isCounsellor) {
        counsellorInfo = {...profileInfo};
        delete counsellorInfo['logo'];
        delete counsellorInfo['logo_image'];
        delete counsellorInfo['domain'];
    } else if(isAdmin) {
        adminInfo = {...profileInfo};
        delete adminInfo['logo'];
        delete adminInfo['logo_image'];
        delete adminInfo['domain'];
    }



    useEffect(() => {
        if(user && user.id){
            (async () => {
                try {
                    let result = await getUserDetails(user.id);
                    if(result?.error?.name === 'UnauthorizedError'){
                        setOpenModal(true);
                        setMsg('Your session has expired. You will be logged out.');
                    }
                    if(result && result.role && result.role.type === 'admin') {
                        setProfileInfo({
                            ...profileInfo,
                            username: result.username || '',
                            email: result.email || '',
                            created_at: moment(result.createdAt).format('MMMM Do YYYY') || '',
                            logo_image: result.cover_image || '',
                            domain: result && result.domain || ''
                        });
                    } else if (result && result.role && result.role.type === 'center') {
                        setProfileInfo({
                            ...profileInfo,
                            center_id: result && result.username || '',
                            name: result && result.center && result.center.name || '',
                            email: result && result.email || '',
                            address: result && result.center && result.center.street_address1 || '',
                            phone_number: result && result.center && result.center.phone_number || '',
                            logo_image: result && result.center && result.center.logo_image || '',
                            domain: result && result.domain || ''
                        });
                    } else if (result && result.role && result.role.type === 'counsellor') {
                        let obj = {
                            first_name: result && result.counsellor && result.counsellor.first_name || '',
                            last_name: result && result.counsellor && result.counsellor.last_name || '',
                            email: result && result.email || '',
                            phone: {
                                area_code: result && result.counsellor && result.counsellor.area_code || '',
                                phone_number: result && result.counsellor && result.counsellor.phone_number || ''
                            },
                            logo_image: result && result.counsellor && result.counsellor.logo_image || '',
                            domain: result && result.domain || ''
                        }
                        setProfileInfo(obj);
                    }
                } catch(err){}
            })();
        }
    }, []);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    function handleClose() {
        setAnchorEl(null);
    }

    function getImg() {
        let img = null;

        const {
            logo_image,
            domain
        } = profileInfo;

        img = logo_image ? domain + logo_image : null;
    
        return img;
    }

    function capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    function capitalizeLabel(val) {
        let res = '';
        let arr = val.split(' ');
        arr.forEach(el => {
            res += capitalizeFirstLetter(el);
            res += ' ';
        })
        return res.trim();
    }
    
    const renderContent = (key, info) => {
        let content = null;
        if(key === 'phone'){
            content = (
                <div className='phone-field'>
                    <label>{capitalizeLabel("phone_number".replace('_', ' ').replace(/([a-z])([A-Z])/g, '$1 $2'))}</label>
                    <span>+{info[key].area_code}{info[key].phone_number}</span>
                </div>
            )
        } else {
            content = (
                <div className="profile-field">
                    <label>{capitalizeLabel(key.replace('_', ' ').replace(/([a-z])([A-Z])/g, '$1 $2'))}</label>
                    <span>{info[key]}</span>
                </div>
            )
        }
        
        return content;
    }

   
    return (
        <div className="Profile-container">
            <div className='profile-box'>
                <img src={getImg() || defaultImg} className="profile-pic" />
                <h3>{isAdmin ? (user && user.username) : (isCenter ? (profileInfo && profileInfo.name) : (isCounsellor ? (user && user.counsellor && user.counsellor.name): null))}</h3>
                <p className='role'>{isAdmin ? 'Admin' : (isCenter ? 'Center' : 'Counselor')}</p>
                
                <button 
                    type="edit-button"
                    onClick={() => navigate("/profile/edit")}
                >
                    <span>Edit</span>
                </button>
                    
                <div className="profile-bottom">
                    <Grid container rowSpacing={2} columnSpacing={6}>
                        {isAdmin && Object.keys(adminInfo).map((el, i) =>  (
                            <Grid item key={i} lg={6} md={6} sm={12} xs={12}>
                                {renderContent(el,adminInfo)}
                            </Grid>
                        ))}

                        {isCenter && Object.keys(centerInfo).map((el, i) =>  (
                            <Grid item key={i} lg={6} md={6} sm={12} xs={12}>
                                {renderContent(el, centerInfo)}
                            </Grid>
                        ))}

                        {isCounsellor && Object.keys(counsellorInfo).map((el, i) =>  (
                            <Grid item key={i} lg={6} md={6} sm={12} xs={12}>
                                {renderContent(el, counsellorInfo)}
                            </Grid>
                        ))}
                    </Grid>
                    <Button
                        color="primary"
                        variant="contained"
                        className="btn-password"
                        onClick={() => navigate('/changePassword')}
                    >
                        Change Password
                    </Button>
                    {/* <div><a href={`https://zoom.us/oauth/authorize?response_type=code&client_id=UccsI5XcRBSIKAtmDN3WQ&redirect_uri=http%3A%2F%2Flocalhost%3A3000%2Foauth-completed`}>Connect Zoom</a></div> */}
                
                </div>
            </div>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </div>
    )
}

export default Profile;